import common from "../../common/common.js"
import HMmessages from "../../components/HM-messages/HMmessages.vue"
import TColorPicker from "../../components/t-color-picker/t-color-picker";
import UploadImages from "../../components/colorui/components/uploadImages";
var serverUrl = common.serverUrl
var keyUtil = common.keyUtil()
var _self;
export default {
	components:{
		HMmessages,
		TColorPicker,
		UploadImages
	},
	data() {
		return {
			isPost:false,
			imgUrl:"",
			tempFace: "",
			userInfo:{},
			token:'',
			type:'',
			imgs:[],
			color: {
				r: 255,
				g: 0,
				b: 0,
				a: 0.6
			},
			isEdit: false,
			fontColor: '#000',
			formats: {},
			readOnly: false,
			placeholder: '描述下你的旅行心得,帮助更多旅行的人...',
			editorHeight: 300,
			keyboardHeight: 0,
			isIOS: false,
			articleId:"",
			title:""
		};
	},
	onLoad() {
		_self = this;
		var draft =uni.getStorageSync('myDraft')
		if(draft!==null&&draft!==""&&draft!==undefined){
			this.articleId = draft.articleId
		}else{
			var articleId = this.key()
			this.articleId = articleId
			
		}
	},
	onShow(){

		var userInfo = uni.getStorageSync("globalUser")
		if(userInfo!==null&&userInfo!==""&&userInfo!==undefined){
			this.userInfo=userInfo
			this.token="Bearer "+this.userInfo.token
			this.type=this.userInfo.tokenType
		}
		
	},
	onUnload(){
	},

	methods: {
		onStatusChange(){
			this.editorCtx.getContents({
				success: function(res) {
					console.log(res)
					console.log(res.html)
				}
			});
		},
		key(){
			 function S4() {
			        return (((1 + Math.random()) * 0x10000) | 0).toString(16).substring(1);
			    }
			    return (S4() + S4() + "" + S4() + "" + S4() + "" + S4() + "" + S4() + S4() + S4());
		},
		upload(){

			// if(this.imgs.length===0){
			// 	this.HMmessages.show("成功",{iconColor:'#ffffff',fontColor:'#ffffff', background:"#ffd655"})
			// }
			var i = 0
			var length = this.imgs.length
			this.upLoadApi(i,length)
		},
		upLoadApi(i,length){
			var userInfo =this.getGlobalUser("globalUser")
			uni.uploadFile({
				url:serverUrl+'/oss/1?parentId='+this.articleId,
				filePath:this.imgs[i],
				name:"file",
				success: (uploadFileRes) => {
					debugger
					var response = JSON.parse(uploadFileRes.data)
					console.log(response.data)
					if(response.code===0){
						i++
						console.log(i)
					if (i < length) {
						// debugger
						this.upLoadApi(i,length);//upLoadApi undefined
					}else{
						return
					}
				}
				},
				complete(){}
				}
		)},

		// 重新选择头像
		changePendingFace(){
			uni.chooseImage({
				count:9,
				sizeType:["compressed"],
				sourceType:["album","camera"],
				success: (res) => {
					//获得临时路径
				// var tempFilePath = res.tempFilePaths[0]
				// console.log(res.tempFilePaths)
				// this.imgUrl=tempFilePath
				// console.log(this.imgUrl)

				this.imgs.push(res.tempFilePaths[0])
				console.log(this.imgs)
				}
			})
		},
		cancel() {
			this.isEdit = false;
		},
		send(){
			var articleId =this.articleId
			var userId = this.userInfo.userId
			var title = this.title
			var token = this.token
			var type = this.type
			if(this.title===""){
				this.warning("标题不能为空哟!")
				return
			}
			this.editorCtx.getContents({
				success: (res)=> {
					var htmlContent = res.html
					console.log(res.html.length)
					if(htmlContent.length<=42){
						this.warning("内容不能为空哟!")
						return
					}
					uni.request({
						header:{
							"Authorization":token,
							"type":type
						},
						url:serverUrl+'/articles/',
						data:{
							"articleId":articleId,
							"title":title,
							"htmlContent":htmlContent,
							"userId":userId,
						},
						method:'POST',
						success: (res) => {
							if(res.data.code===0){
								uni.showToast({
									title:res.data.msg,
									duration:1500
								})
							}
						},

					});
				}
			});
		},
		open() {
			this.$refs.colorPicker.open();
			this.isEdit = true;
			// uni.hideKeyboard();
		},
		hideKey() {
			uni.hideKeyboard();
		},
		async confirm(e) {
			this.isEdit = false;
			this.fontColor = await e.hex;
			this.onStatusChange({
				detail: {
					color: e.hex
				}
			});
			this.$forceUpdate();
		},
		readOnlyChange() {
			this.readOnly = !this.readOnly
		},
		onEditorReady() {
			uni.createSelectorQuery().select('#editor').context(function(res) {
				_self.editorCtx = res.context;
			}).exec();
		},
		undo() {
			this.editorCtx.undo();
		},

		redo() {
			this.editorCtx.redo();
		},

		blur() {
			this.editorCtx.blur();
		},

		format(e) {
			// this.hideKey();
			let {
				name,
				value
			} = e.target.dataset;
			if (!name) return; // console.log('format', name, value)
			this.editorCtx.format(name, value);
		},

		onStatusChange(e) {
			this.formats = e.detail;
		},

		insertDivider() {
			this.editorCtx.insertDivider({
				success: function() {
					console.log('insert divider success');
				}
			});
		},

		store(e) {
			this.editorCtx.getContents({
				success: function(res) {
					e.currentTarget.id == 1 ? console.log('保存内容:', res.html) : uni.navigateTo({
						url: `./components/preview?rich=${encodeURIComponent(res.html)}`
					});
				}
			});
		},

		clear() {
			this.editorCtx.clear({
				success: function(res) {
					console.log("clear success");
				}
			});
		},

		removeFormat() {
			this.editorCtx.removeFormat();
		},

		insertDate() {
			const date = new Date();
			const formatDate = `${date.getFullYear()}/${date.getMonth() + 1}/${date.getDate()}`;
			this.editorCtx.insertText({
				text: formatDate
			});
		},

		insertImage() {
			// const that = this;
			var token = this.token
			var type = this.type
			var articleId = this.articleId
			uni.chooseImage({
				count: 1,
				success: function(res) {
					uni.uploadFile({
						header:{
							"Authorization":token,
							"type":type
						},
						url:serverUrl+'/oss/1?parentId='+articleId,
						filePath: res.tempFilePaths[0],
						name:"file",
						method:'POST',
						success: (re) => {
							var mydata = JSON.parse(re.data)
							_self.editorCtx.insertImage({
								src: mydata.data,
								data: {
									id: 'myImg',
									role: 'god',
									class:'img'
								},
								width: '30%',
								success: function() {
									// console.log('insert image success');
								}
							});

						},
					})

				}
			});
		},
		warning(msg) {
			this.HMmessages.show(msg, {iconColor: '#ffffff', fontColor: '#ffffff', background: "#ffd655"})
		}
		}
}
